import React from 'react';
import { connect } from 'react-redux';
import {get_inspections} from "../../../redux/actions/inspector/inspectorActions";
import {InspectionsListComponent} from "../../shared/Components";

const AllInspectionsComponent = ( { inspections, user } ) => {
    return <div className={'container'}>
        <h2>All Inspections</h2>
        <InspectionsListComponent inspections={inspections} user={user}/>
    </div>;
};
const mapStateToProps = ({inspector_inspections, user}) => {
    return  { inspections: inspector_inspections.inspections, user };
};
const mapDispatchToProps = dispatch => {
    dispatch(get_inspections('all'));
    return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(AllInspectionsComponent);



