import React, {Fragment} from 'react';
import InspectionsListRowComponent from "./InspectionListRow";
const InspectionsListComponent = ({ inspections, user }) => {
    return (
        <Fragment>
            {renderContent(inspections, user)}
        </Fragment>
    );
};
const renderContent = ( inspections, user ) => {
    if (inspections.length) {
        return (
            <div className={"inspections-list"}>
                <ul className="inspections-list-heading">
                    <li>Type</li>
                    <li>Date</li>
                    <li>Due Date</li>
                    <li>Status</li>
                    <li>Action</li>
                </ul>
                {inspections.map( (inspection, index) => <InspectionsListRowComponent
                    key={index}
                    user={user}
                    inspection={inspection} /> )}
            </div>
        )
    }
};
export {InspectionsListComponent};



