import React from 'react';
import { connect } from 'react-redux';
import { getInspections } from "../../../redux/actions/client/inspectionsActions";
import "../../../assets/styles/home.css";
import {InspectionsListComponent} from "../../shared/Components";
const HomeComponent = ({ client_inspections, user }) => {
    return (
        <div className={"container"}>
            <h2>Dashboard</h2>
            <h2>Summary</h2>
            <InspectionsListComponent inspections={ client_inspections } user={user}/>
        </div>
    )
};
const mapStateToProps = ({ client_inspections, user }) => {
    return { ...client_inspections, user };
};
const mapDispatchToProps = dispatch => {
    dispatch(getInspections("dashboard"));
    return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(HomeComponent);



